header {
    background-color: #e1e6ef;
    max-height: 56px;
    padding: 10px;
    color: #393f53;
    h1, .buttons {
        display: inline; } }
.buttons {
        margin-left: 73%; }
body {
    min-height: 972px; }
.main-content {
    display: grid;
    .pages {
        border: 1px black solid;
        grid-row: 1;
        grid-column: 1 / span 3;
        padding: 10px;
        .col {
            display: inline;
            &:nth-child(2) {
                margin-left: 30%; }
            &:last-child {
                float: right; } } }
    .customers-table {
        border: 1px black solid;
        grid-row: 2 / span 6;
        grid-column: 1 / span 3;
        padding: 20px; }
    .filters {
        border: 1px black solid;
        background-color: #e1e6ef;
        grid-row: 1 / span 7;
        grid-column: 4;
        padding: 15px;
        h2,input[type=button] {
            display: inline; } } }
div#alphabet {
    padding: 10px;
    a {
        padding-left: 2px;
        padding-right: 2px;
        text-transform: uppercase;
        text-decoration: none;
        color: #8f949d;
        font-size: 15px; } }
@media screen and (max-width: 1099px) {
    .buttons {
        margin-left: 69%; } }
@media screen and (max-width: 970px) {
    .main-content {
        .pages {
            .col {
                &:nth-child(2) {
                    margin-left: 18%; } } } }
    .buttons {
        margin-left: 63%; } }
@media screen and (max-width: 870px) {
    .buttons {
        margin-left: 59%; } }
@media screen and (max-width: 779px) {
    .main-content {
        .pages {
            .col {
                &:nth-child(2) {
                    margin-left: 5%; } } } }
    .buttons {
        margin-left: 53%; } }
@media screen and (max-width: 690px) {
    .buttons {
        margin-left: 40%; } }
@media screen and (max-width: 600px) {
    .buttons {
        margin-left: 33%; } }
